'use strict';

/**
* Boolean indicating if the runtime is a web browser.
*
* @module @stdlib/assert/is-browser
*
* @example
* var IS_BROWSER = require( '@stdlib/assert/is-browser' );
*
* var bool = IS_BROWSER;
* // returns <boolean>
*/

// MODULES //

var isBrowser = require( './main.js' );


// MAIN //

var bool = isBrowser();


// EXPORTS //

module.exports = bool;
